import CryptoJS from 'crypto-js';

/**
 * Classe scurise pour le chiffrement et dchiffrement
 */
class AlphaEncryptor {
  constructor() {
    this.encryptionKey = process.env.REACT_APP_ALPHA;
    
    if (!this.encryptionKey) {
      console.error('Erreur: Cl de chiffrement non dfinie dans .env');
    }
  }
  
  /**
   * Chiffre une valeur
   * @param {string} value - Valeur  chiffrer
   * @returns {string} - Valeur chiffre
   */
  encryptAlpha(value) {
    if (!this.encryptionKey) {
      throw new Error('Cl de chiffrement non configure');
    }
    
    if (!value) {
      throw new Error('Valeur  chiffrer non dfinie');
    }
    
    try {
      const encrypted = CryptoJS.AES.encrypt(value, this.encryptionKey).toString();
      return encrypted;
    } catch (error) {
      console.error('Erreur lors du chiffrement:', error);
      throw error;
    }
  }
  
  /**
   * Dchiffre une valeur et effectue un decodeURIComponent en toute scurit
   * Version robuste qui gre les erreurs de format UTF-8
   * @param {string} encryptedValue - Valeur chiffre
   * @returns {string|null} - Valeur dchiffre et dcode, ou null en cas d'erreur
   */
  decryptAndDecode(encryptedValue) {
    try {
      if (!encryptedValue) {
        return null;
      }
      
      // Vrifier si la valeur a besoin d'tre dcode avant dchiffrement
      let valueToDecrypt = encryptedValue;
      
      // Stratgie 1: Dchiffrer directement puis dcoder
      try {
        // Tentative standard de dchiffrement
        const decrypted = this.safeDecrypt(valueToDecrypt);
        
        if (!decrypted) {
          throw new Error('Dchiffrement direct chou');
        }
        
        // Tenter de dcoder si c'est une URI encode
        try {
          return decodeURIComponent(decrypted);
        } catch (uriError) {
          // Si ce n'est pas une URI encode, retourner la valeur dchiffre
          return decrypted;
        }
      } catch (firstAttemptError) {
        // Stratgie 2: Dcoder d'abord, puis dchiffrer
        try {
          // Essayer de dcoder l'URI avant de dchiffrer
          const decodedValue = decodeURIComponent(encryptedValue);
          const decrypted = this.safeDecrypt(decodedValue);
          
          if (!decrypted) {
            throw new Error('Dchiffrement aprs dcodage chou');
          }
          
          return decrypted;
        } catch (secondAttemptError) {
          // Stratgie 3: Dernire tentative avec diffrentes combinaisons
          try {
            // Essayer d'abord de dcoder deux fois (cas o double encodage)
            const doubleDecodedValue = decodeURIComponent(decodeURIComponent(encryptedValue));
            return this.safeDecrypt(doubleDecodedValue);
          } catch (finalError) {
            console.error('Toutes les tentatives de dchiffrement ont chou');
            return null;
          }
        }
      }
    } catch (error) {
      console.error('Erreur lors du dchiffrement ou dcodage:', error);
      return null;
    }
  }
  
  /**
   * Mthode interne pour dchiffrer de manire scurise sans lancer d'exception
   * @private
   * @param {string} value - Valeur  dchiffrer
   * @returns {string|null} - Valeur dchiffre ou null en cas d'erreur
   */
  safeDecrypt(value) {
    try {
      if (!value) return null;
      
      const bytes = CryptoJS.AES.decrypt(value, this.encryptionKey);
      const decrypted = bytes.toString(CryptoJS.enc.Utf8);
      
      return decrypted || null;
    } catch (error) {
      return null;
    }
  }
  
  /**
   * Enregistre une valeur chiffre dans le localStorage avec encodage cohrent
   * @param {string} key - Cl de stockage 
   * @param {string} value - Valeur  chiffrer et stocker
   * @returns {boolean} - Succs de l'opration
   */
  saveToStorage(key, value) {
    try {
      if (!key || !value) {
        return false;
      }
      
      // 1. Chiffrer la valeur
      const encrypted = this.encryptAlpha(value);
      
      // 2. Encoder en URI pour viter les problmes de caractres spciaux
      localStorage.setItem(key, encodeURIComponent(encrypted));
      
      // Vrification que le stockage a fonctionn
      return !!localStorage.getItem(key);
    } catch (error) {
      console.error('Erreur lors de l\'enregistrement dans le localStorage:', error);
      return false;
    }
  }
  
  /**
   * Rcupre et dchiffre une valeur du localStorage
   * @param {string} key - Cl de stockage
   * @returns {string|null} - Valeur dchiffre ou null
   */
  getFromStorage(key) {
    try {
      const storedValue = localStorage.getItem(key);
      
      if (!storedValue) {
        return null;
      }
      
      // Tenter plusieurs approches pour rcuprer la valeur correctement
      return this.decryptAndDecode(storedValue);
    } catch (error) {
      console.error('Erreur lors de la rcupration depuis le localStorage:', error);
      return null;
    }
  }
}

const alphaEncryptor = new AlphaEncryptor();

export default alphaEncryptor;